.list {
  display: flex;
  flex-direction: column;
  gap: 14px;
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  margin-top: 20px;
}
.item {
  display: flex;
  align-items: center;
  width: 400px;
  min-height: 110px;
  padding: 8px 16px;
  box-shadow: 0 2px 3px 3px black;
}
